html, body {
  width: 100%;
  height: auto;
  margin: 0px;
  padding: 0px;
  overflow-x: hidden;
}

@media (max-width: 1387px){
  .projects {
    height: auto;
  }
}

@media (max-width: 900px){
  .about-content {
    -ms-flex-direction: column;
        flex-direction: column
  }
  .profile-div {
    -ms-flex-pack: center;
        justify-content: center;
    margin-right: 0px;
    margin-bottom: 30px;
    width: auto;
  }
  .profile {
    width: 38%
  }
  .nav {
    background-color: #2d2d2dc2
  }

}

@media (max-width: 630px){
  .titleName {
    font-size: 4rem
  }
  .mainTitle {
    font-size: 2.1rem
  }
}

@media (max-width: 450px) {
  .nav {
    -ms-flex-pack: center;
        justify-content: center;
  }
}



